body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header-title {
    font-family: "HelveticaNeue-Thin", "Helvetica Neue Thin", "Helvetica Neue";
    font-weight: 200;
    font-size: 80px;
    color: #898989;
}

.Header-subtitle {
    font-family: "HelveticaNeue-UltraLight", "Helvetica Neue Ultra Light", "Helvetica Neue";
    font-weight: 100;
    font-size: 30px;
    color: #898989;
}
.RecentWork-description {
    font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue";
    font-weight: 300;
    font-size: 28px;
    line-height: 36px;
    text-align: center;
    color: #6E6E6E;
}

.GooglePlayButton {
    background: url("/assets/google_play.png");
    width: 277px;
    height: 83px;
    border: none;
    outline: none;
    cursor: pointer;
}
.App-title {
    font-family: "HelveticaNeue-Thin", "Helvetica Neue Thin", "Helvetica Neue";
    font-weight: 200;
    font-size: 58px;
    color: #6E6E6E;
}
.Hire-placeholder {
    font-family: "HelveticaNeue-Thin", "Helvetica Neue Thin", "Helvetica Neue";
    font-weight: 200;
    font-size: 28px;
    color: #6A6B68;
}

.Input-email {
    width: inherit;
    font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue";
    font-weight: 200;
    font-size: 28px;
    color: #6E6E6E;
    border-color: rgb(238, 238, 238);
    border-style: solid;
    border-top: unset;
    border-left: unset;
    border-right: unset;
    border-width: 2px;
    outline: none;
}

::-webkit-input-placeholder {
    color: #ACACAC;
}

::-ms-input-placeholder {
    color: #ACACAC;
}

::placeholder {
    color: #ACACAC;
}

.Input-message {
    font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue";
    font-weight: 200;
    font-size: 28px;
    color: #6E6E6E;
    border-width: 2px;
    border-radius: 10px;
    border-color: rgb(238, 238, 238);
    border-style: solid;
    padding: 20px;
    outline: none;
    height: 186px;
    text-align: start;
    resize: none;
    width: 620px;
}

.Attach-button {
    font-family: "HelveticaNeue-Medium", "Helvetica Neue Medium", "Helvetica Neue";
    font-weight: 500;
    font-size: 28px;
    text-align: center;
    color: #BD7676;
    background-color: unset;
    outline: none;
    border: none;
}

.Submit-button {
    font-family: "HelveticaNeue-Bold", "Helvetica Neue Bold", "Helvetica Neue";
    font-weight: bold;
    font-size: 30px;
    text-align: center;
    color: white;
    background-color: #BD7676;
    outline: none;
    border: none;
    height: 93px;
    width: 277px;
    border-radius: 10px;
}

.Contact-form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 660px;
}

.fade-appear {
    opacity: 0;
}

.fade-appear.fade-appear-active {
    opacity: 1;
    -webkit-transition: opacity 300ms linear;
    transition: opacity 300ms linear;
}

.fade-exit {
    opacity: 1;
}

.fade-exit.fade-exit-active {
    opacity: 0;
    -webkit-transition: opacity 300ms linear;
    transition: opacity 300ms linear;
}

.fade-exit-done {
    opacity: 0;
}

.fade-enter {
    opacity: 0;
    height: inherit;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    -webkit-transition: opacity 300ms linear;
    transition: opacity 300ms linear;
}


